webpackJsonp([155930537874631],{

/***/ 843:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"victorypolaraxis\"><a href=\"#victorypolaraxis\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>VictoryPolarAxis</h1>\n<p>VictoryPolarAxis renders a single axis which can be used on its own or composed with <a href=\"https://formidable.com/open-source/victory/docs/victory-chart\"><code class=\"language-text\">VictoryChart</code></a>.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;svg width={400} height={400}&gt;\n  &lt;VictoryPolarAxis\n    width={400}\n    height={400}\n    theme={VictoryTheme.material}\n    standalone={false}\n  /&gt;\n  &lt;VictoryPolarAxis dependentAxis\n    width={400}\n    height={400}\n    domain={[0, 10]}\n    theme={VictoryTheme.material}\n    standalone={false}\n  /&gt;\n&lt;/svg&gt;</code></pre>\n<h2 id=\"props\"><a href=\"#props\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Props</h2>\n<h3 id=\"animate\"><a href=\"#animate\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>animate</h3>\n<p><code class=\"language-text\">type: boolean || object</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">animate</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#animate\">Read about it here</a></p>\n<p>See the <a href=\"https://formidable.com/open-source/victory/guides/animations\">Animations Guide</a> for more detail on animations</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">animate<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>\n  duration<span class=\"token punctuation\">:</span> <span class=\"token number\">2000</span><span class=\"token punctuation\">,</span>\n  easing<span class=\"token punctuation\">:</span> <span class=\"token string\">\"bounce\"</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"axisangle\"><a href=\"#axisangle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>axisAngle</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">axisAngle</code> prop is used to position the dependent axis. This prop should be given in degrees. Degrees are defined as starting at the 3 o’clock position, and proceeding counterclockwise. This prop only affects the dependent axis.</p>\n<p><em>default:</em> <code class=\"language-text\">axisAngle={0}</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis dependentAxis\n  theme={VictoryTheme.material}\n  axisAngle={45}\n/&gt;</code></pre>\n<h3 id=\"axiscomponent\"><a href=\"#axiscomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>axisComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">axisComponent</code> prop takes a component instance which will be responsible for rendering an axis line for the dependent axis. The independent axis renders a <code class=\"language-text\">circularAxisComponent</code>. The new element created from the passed <code class=\"language-text\">axisComponent</code> will be provided with the following props calculated by <code class=\"language-text\">VictoryPolarAxis</code>: <code class=\"language-text\">x1</code>, <code class=\"language-text\">y1</code>, <code class=\"language-text\">x2</code>, <code class=\"language-text\">y2</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If an <code class=\"language-text\">axisComponent</code> is not provided, <code class=\"language-text\">VictoryPolarAxis</code> will use its default <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#linesegment\">LineSegment component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">axisComponent={&lt;LineSegment type={&quot;axis&quot;}/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">axisComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>LineSegment</span> <span class=\"token attr-name\">events</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> onClick<span class=\"token punctuation\">:</span> handleClick <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"axislabelcomponent\"><a href=\"#axislabelcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>axisLabelComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">axisLabelComponent</code> prop takes a component instance which will be used to render the axis label. The new element created from the passed <code class=\"language-text\">axisLabelComponent</code> will be supplied with the following props: <code class=\"language-text\">x</code>, <code class=\"language-text\">y</code>, <code class=\"language-text\">verticalAnchor</code>, <code class=\"language-text\">textAnchor</code>, <code class=\"language-text\">angle</code>, <code class=\"language-text\">transform</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If <code class=\"language-text\">axisLabelComponent</code> is omitted, a new <a href=\"https://formidable.com/open-source/victory/docs/victory-label\"><code class=\"language-text\">VictoryLabel</code></a> will be created with props described above.</p>\n<p><strong>Note:</strong> Axis labels are only rendered with the dependent axis in polar charts.</p>\n<p><em>default:</em> <code class=\"language-text\">axisLabelComponent={&lt;VictoryLabel/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">axisLabelComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>VictoryLabel</span> <span class=\"token attr-name\">dy</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">20</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"axisvalue\"><a href=\"#axisvalue\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>axisValue</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">axisValue</code> prop may be used instead of <code class=\"language-text\">axisAngle</code> to position the dependent axis. Ths prop is useful when dependent axes should line up with values on the independent axis.</p>\n<pre class=\"pre\"><code class=\"language-playground\"> &lt;VictoryChart polar\n  theme={VictoryTheme.material}\n&gt;\n  {\n    [&quot;intelligence&quot;, &quot;strength&quot;, &quot;speed&quot;, &quot;stealth&quot;, &quot;charisma&quot;].map((d, i) =&gt; {\n      return (\n        &lt;VictoryPolarAxis dependentAxis\n          key={i}\n          label={d}\n          labelPlacement=&quot;perpendicular&quot;\n          style={{ tickLabels: { fill: &quot;none&quot; } }}\n          axisValue={i}\n        /&gt;\n      );\n    })\n  }\n  &lt;VictoryBar\n    style={{ data: { fill: &quot;tomato&quot;, width: 25 } }}\n    data={[\n      { x: 0, y: 10 },\n      { x: 1, y: 25 },\n      { x: 2, y: 40 },\n      { x: 3, y: 50 },\n      { x: 4, y: 50 }\n    ]}\n  /&gt;\n&lt;/VictoryChart&gt;</code></pre>\n<h3 id=\"circularaxiscomponent\"><a href=\"#circularaxiscomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>circularAxisComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">circularAxisComponent</code> prop takes a component instance which will be responsible for rendering an axis arc for the independent axis. The dependent axis renders an <code class=\"language-text\">axisComponent</code>. The new element created from the passed <code class=\"language-text\">circularAxisComponent</code> will be provided with the following props calculated by <code class=\"language-text\">VictoryPolarAxis</code>: <code class=\"language-text\">style</code>, <code class=\"language-text\">events</code>,\n<code class=\"language-text\">cx</code>, <code class=\"language-text\">cy</code>, <code class=\"language-text\">r</code>, <code class=\"language-text\">startAngle</code>, and <code class=\"language-text\">endAngle</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If a <code class=\"language-text\">circularAxisComponent</code> is not provided, <code class=\"language-text\">VictoryPolarAxis</code> will use its default <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#arc\">Arc component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">circularAxisComponent={&lt;Arc type={&quot;axis&quot;}/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">circularAxisComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Arc</span> <span class=\"token attr-name\">r</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">300</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"circulargridcomponent\"><a href=\"#circulargridcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>circularGridComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">circularGridComponent</code> prop takes a component instance which will be responsible for rendering a grid element. The new element created from the passed <code class=\"language-text\">circularGridComponent</code> will be provided with the following props calculated by <code class=\"language-text\">VictoryPolarAxis</code>: <code class=\"language-text\">x1</code>, <code class=\"language-text\">y1</code>, <code class=\"language-text\">x2</code>, <code class=\"language-text\">y2</code>, <code class=\"language-text\">tick</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If a <code class=\"language-text\">circularGridComponent</code> is not provided, <code class=\"language-text\">VictoryPolarAxis</code> will use its default <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#arc\">Arc component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">circularGridComponent={&lt;Arc type={&quot;grid&quot;}/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">circularGridComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Arc</span> <span class=\"token attr-name\">r</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">300</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"containercomponent\"><a href=\"#containercomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>containerComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">containerComponent</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#containercomponent\">Read about it here</a></p>\n<p>The following victory containers work with polar charts:\n<code class=\"language-text\">VictoryContainer</code>, <code class=\"language-text\">VictorySelectionContainer</code>, <code class=\"language-text\">VictoryVoronoiContainer</code> and <code class=\"language-text\">VictoryZoomContainer</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">containerComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>VictoryVoronoiContainer</span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"dependentaxis\"><a href=\"#dependentaxis\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>dependentAxis</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>The <code class=\"language-text\">dependentAxis</code> boolean prop specifies whether the axis corresponds to the dependent variable (usually y, or the radial dimension on a polar chart). This prop is useful when composing <code class=\"language-text\">VictoryPolarAxis</code> with other components to form a chart.</p>\n<p><em>default:</em> <code class=\"language-text\">dependentAxis={false}</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;div style={{ display: &quot;flex&quot; }}&gt;\n  &lt;VictoryPolarAxis dependentAxis theme={VictoryTheme.material} /&gt;\n  &lt;VictoryPolarAxis theme={VictoryTheme.material} /&gt;\n&lt;/div&gt;</code></pre>\n<h3 id=\"domain\"><a href=\"#domain\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>domain</h3>\n<p><code class=\"language-text\">type: array[low, high] || { x: [low, high], y: [low, high] }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">domain</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#domain\">Read about it here</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">domain<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>x<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">100</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"domainpadding\"><a href=\"#domainpadding\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>domainPadding</h3>\n<p><code class=\"language-text\">type: number || array[left, right] || { x: [left, right], y: [bottom, top] }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">domainPadding</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#domainpadding\">Read about it here</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">domainPadding<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>x<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token number\">10</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">-</span><span class=\"token number\">10</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">5</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"endangle\"><a href=\"#endangle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>endAngle</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">endAngle</code> props defines the overall end angle of the axis in degrees. This prop is used in conjunction with <code class=\"language-text\">startAngle</code> to create an axis that spans only a segment of a circle, or to change overall rotation of the axis. This prop should be given as a number of degrees. Degrees are defined as starting at the 3 o’clock position, and proceeding counterclockwise.</p>\n<p><em>default:</em> <code class=\"language-text\">endAngle={360}</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;div style={{ display: &quot;flex&quot; }}&gt;\n&lt;VictoryPolarAxis\n  startAngle={90}\n  endAngle={450}\n  tickValues={[0, 90, 180, 270]}\n  labelPlacement=&quot;vertical&quot;\n/&gt;\n&lt;VictoryPolarAxis\n  startAngle={0}\n  endAngle={180}\n  tickValues={[0, 45, 90, 135, 180]}\n/&gt;\n&lt;/div&gt;</code></pre>\n<h3 id=\"events\"><a href=\"#events\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>events</h3>\n<p><code class=\"language-text\">type: array[objects]</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">events</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#events\">Read about it here</a></p>\n<p>See the <a href=\"https://formidable.com/open-source/victory/guides/events\">Events Guide</a>for more information on defining events.</p>\n<p><strong>note:</strong> valid event targets for <code class=\"language-text\">VictoryPolarAxis</code> are “axis”, “axisLabel”, “grid”, “ticks”, and “tickLabels”.\nTargets that correspond to only one element {“axis” and “axisLabel”) should use the special eventKey “all”.</p>\n<h3 id=\"externaleventmutations\"><a href=\"#externaleventmutations\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>externalEventMutations</h3>\n<p><code class=\"language-text\">type: array[object]</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">externalEventMutations</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#externalEventsMutations\">Read about it in detail</a></p>\n<h3 id=\"gridcomponent\"><a href=\"#gridcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>gridComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">gridComponent</code> prop takes a component instance which will be responsible for rendering a grid element. The new element created from the passed <code class=\"language-text\">gridComponent</code> will be provided with the following props calculated by <code class=\"language-text\">VictoryPolarAxis</code>: <code class=\"language-text\">x1</code>, <code class=\"language-text\">y1</code>, <code class=\"language-text\">x2</code>, <code class=\"language-text\">y2</code>, <code class=\"language-text\">tick</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If a <code class=\"language-text\">gridComponent</code> is not provided, <code class=\"language-text\">VictoryPolarAxis</code> will use its default <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#linesegment\">LineSegment component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">gridComponent={&lt;LineSegment type={&quot;grid&quot;}/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">gridComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>LineSegment</span> <span class=\"token attr-name\">events</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> onClick<span class=\"token punctuation\">:</span> handleClick <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"groupcomponent\"><a href=\"#groupcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>groupComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">groupComponent</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#groupcomponent\">Read about it here</a></p>\n<p><em>default:</em> <code class=\"language-text\">&lt;g/&gt;</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">groupComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>g</span> <span class=\"token attr-name\">transform</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>translate(10, 10)<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"height\"><a href=\"#height\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>height</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">height</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#height\">Read about it here</a></p>\n<p><em>default (provided by default theme):</em> <code class=\"language-text\">height={300}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">height<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">400</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"innerradius\"><a href=\"#innerradius\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>innerRadius</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>When the <code class=\"language-text\">innerRadius</code> prop is set, polar axes will be hollow rather than circular.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis theme={VictoryTheme.material} innerRadius={50}/&gt;</code></pre>\n<h3 id=\"invertaxis\"><a href=\"#invertaxis\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>invertAxis</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>The <code class=\"language-text\">invertAxis</code> boolean prop specifies whether the domain for a given axis should be inverted. By default, axes will be displayed with lower values on the bottom / left, and higher values on the top / right regardless of orientation.</p>\n<p><em>default:</em> <code class=\"language-text\">invertAxis={false}</code></p>\n<h3 id=\"label\"><a href=\"#label\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>label</h3>\n<p><code class=\"language-text\">type: string</code></p>\n<p>The <code class=\"language-text\">label</code> prop defines the label that will appear with a dependent axis. Labels will not appear with independent polar axes. This prop should be given as a string.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis dependentAxis\n  theme={VictoryTheme.material}\n  label=&quot;wind speed&quot;\n  labelPlacement=&quot;perpendicular&quot;\n/&gt;</code></pre>\n<h3 id=\"labelplacement\"><a href=\"#labelplacement\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>labelPlacement</h3>\n<p><code class=\"language-text\">type: &quot;parallel&quot; || &quot;perpendicular&quot; || &quot;vertical&quot;</code></p>\n<p>The <code class=\"language-text\">labelPlacement</code> prop specifies how tick labels should be placed relative to the angular tick values. Options for this prop are “vertical”, “parallel”, and “perpendicular”.</p>\n<p><em>default:</em> <code class=\"language-text\">labelPlacement=&quot;parallel&quot;</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  labelPlacement=&quot;perpendicular&quot;\n  theme={VictoryTheme.material}\n/&gt;</code></pre>\n<h3 id=\"maxdomain\"><a href=\"#maxdomain\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>maxDomain</h3>\n<p><code class=\"language-text\">type: number || { x: number, y: number }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">maxDomain</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#maxDomain\">Read about it in detail</a></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  dependentAxis\n  tickValues={[1, 2, 3]}\n  maxDomain={5}\n/&gt;</code></pre>\n<h3 id=\"mindomain\"><a href=\"#mindomain\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>minDomain</h3>\n<p><code class=\"language-text\">type: number || { x: number, y: number }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">minDomain</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#minDomain\">Read about it in detail</a></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  dependentAxis\n  tickValues={[2, 3, 4, 5]}\n  minDomain={0}\n/&gt;</code></pre>\n<h3 id=\"name\"><a href=\"#name\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>name</h3>\n<p><code class=\"language-text\">type: string</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">name</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#name\">Read about it here</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">name<span class=\"token operator\">=</span><span class=\"token string\">\"series-1\"</span></code></pre>\n      </div>\n<h3 id=\"origin\"><a href=\"#origin\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>origin</h3>\n<p><code class=\"language-text\">type: { x: number, y: number }</code></p>\n<p><strong>The <code class=\"language-text\">origin</code> prop is only used by polar charts, and is usually controlled by <code class=\"language-text\">VictoryChart</code>. It will not typically be necessary to set an <code class=\"language-text\">origin</code> prop manually</strong></p>\n<p><a href=\"https://formidable.com/open-source/victory/docs/common-props#origin\">Read about the <code class=\"language-text\">origin</code> prop in detail</a></p>\n<h3 id=\"padding\"><a href=\"#padding\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>padding</h3>\n<p><code class=\"language-text\">type: number || { top: number, bottom: number, left: number, right: number }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">padding</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#padding\">Read about it here</a></p>\n<p><em>default (provided by default theme):</em> <code class=\"language-text\">padding={50}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">padding<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> top<span class=\"token punctuation\">:</span> <span class=\"token number\">20</span><span class=\"token punctuation\">,</span> bottom<span class=\"token punctuation\">:</span> <span class=\"token number\">60</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"range\"><a href=\"#range\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>range</h3>\n<p><code class=\"language-text\">type: array[low, high] || { x: [low, high], y: [low, high] }</code></p>\n<p><strong>The <code class=\"language-text\">range</code> prop is usually controlled by <code class=\"language-text\">VictoryChart</code>. It will not typically be necessary to set a <code class=\"language-text\">range</code> prop manually</strong></p>\n<p><a href=\"https://formidable.com/open-source/victory/docs/common-props#range\">Read about the <code class=\"language-text\">range</code> prop in detail</a></p>\n<h3 id=\"scale\"><a href=\"#scale\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>scale</h3>\n<p><code class=\"language-text\">type: scale || { x: scale, y: scale }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">scale</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#scale\">Read about it here</a>\nOptions for scale include “linear”, “time”, “log”, “sqrt” and the <code class=\"language-text\">d3-scale</code> functions that correspond to these options.</p>\n<p><strong>note:</strong> Though <code class=\"language-text\">VictoryPolarAxis</code> can take a <code class=\"language-text\">scale</code> prop with scales defined for both <code class=\"language-text\">x</code> and <code class=\"language-text\">y</code>, only the scale that corresponds the given axis will be used.</p>\n<p><em>default:</em> <code class=\"language-text\">scale=&quot;linear&quot;</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">scale<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>x<span class=\"token punctuation\">:</span> <span class=\"token string\">\"linear\"</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token string\">\"log\"</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"sharedevents\"><a href=\"#sharedevents\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>sharedEvents</h3>\n<p><strong>The <code class=\"language-text\">sharedEvents</code> prop is used internally to coordinate events between components. It should not be set manually.</strong></p>\n<h3 id=\"singlequadrantdomainpadding\"><a href=\"#singlequadrantdomainpadding\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>singleQuadrantDomainPadding</h3>\n<p><code class=\"language-text\">type: boolean || { x: boolean, y: boolean }</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">singleQuadrantDomainPadding</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#singlequadrantdomainpadding\">Read about it here</a></p>\n<h3 id=\"standalone\"><a href=\"#standalone\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>standalone</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">standalone</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#standalone\">Read about it here</a></p>\n<p><strong>note:</strong> When <code class=\"language-text\">VictoryPolarAxis</code> is nested within a component like <code class=\"language-text\">VictoryChart</code>, this prop will be set to <code class=\"language-text\">false</code></p>\n<p><em>default:</em> <code class=\"language-text\">standalone={true}</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;svg width={350} height={350}&gt;\n  &lt;VictoryPolarAxis\n    theme={VictoryTheme.material}\n    standalone={false}\n  /&gt;\n  &lt;VictoryPolarAxis dependentAxis\n    axisAngle={45}\n    labelPlacement=&quot;vertical&quot;\n    theme={VictoryTheme.material}\n    standalone={false}\n  /&gt;\n&lt;/svg&gt;</code></pre>\n<h3 id=\"startangle\"><a href=\"#startangle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>startAngle</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">startAngle</code> props defines the overall end angle of the axis in degrees. This prop is used in conjunction with <code class=\"language-text\">endAngle</code> to create an axis that spans only a segment of a circle, or to change overall rotation of the axis. This prop should be given as a number of degrees. Degrees are defined as starting at the 3 o’clock position, and proceeding counterclockwise.</p>\n<p><em>default:</em> <code class=\"language-text\">startAngle={0}</code></p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;div style={{ display: &quot;flex&quot; }}&gt;\n  &lt;VictoryPolarAxis\n    startAngle={90}\n    endAngle={450}\n    tickValues={[0, 90, 180, 270]}\n    labelPlacement=&quot;vertical&quot;\n  /&gt;\n  &lt;VictoryPolarAxis\n    startAngle={0}\n    endAngle={180}\n    tickValues={[0, 45, 90, 135, 180]}\n  /&gt;\n&lt;/div&gt;</code></pre>\n<h3 id=\"style\"><a href=\"#style\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>style</h3>\n<p><code class=\"language-text\">type: { axis: object, axisLabel: object, grid: object, ticks: object, tickLabels: object }</code></p>\n<p>The <code class=\"language-text\">style</code> prop defines the style of the component. The style prop should be given as an object with styles defined for <code class=\"language-text\">parent</code>, <code class=\"language-text\">axis</code>, <code class=\"language-text\">axisLabel</code>, <code class=\"language-text\">grid</code>, <code class=\"language-text\">ticks</code>, and <code class=\"language-text\">tickLabels</code>. Any valid svg styles are supported, but <code class=\"language-text\">width</code>, <code class=\"language-text\">height</code>, and <code class=\"language-text\">padding</code> should be specified via props as they determine relative layout for components in VictoryChart. Functional styles may be defined for <code class=\"language-text\">grid</code>, <code class=\"language-text\">tick</code>, and <code class=\"language-text\">tickLabel</code> style properties, and they will be evaluated with each tick.</p>\n<p><strong>note:</strong> When a component is rendered as a child of another Victory component, or within a custom <code class=\"language-text\">&lt;svg&gt;</code> element with <code class=\"language-text\">standalone={false}</code> parent styles will be applied to the enclosing <code class=\"language-text\">&lt;g&gt;</code> tag. Many styles that can be applied to a parent <code class=\"language-text\">&lt;svg&gt;</code> will not be expressed when applied to a <code class=\"language-text\">&lt;g&gt;</code>.</p>\n<p><strong>note:</strong> custom <code class=\"language-text\">angle</code> and <code class=\"language-text\">verticalAnchor</code> properties may be included in <code class=\"language-text\">labels</code> styles.</p>\n<p><em>default (provided by default theme):</em> See <a href=\"https://github.com/FormidableLabs/victory/blob/master/packages/victory-core/src/victory-theme/grayscale.js\">grayscale theme</a> for more detail</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  style={{\n    axis: {stroke: &quot;black&quot;},\n    grid: {stroke: (t) =&gt; t &gt; 0.5 ? &quot;red&quot; : &quot;blue&quot;},\n    tickLabels: {fontSize: 15, padding: 15}\n  }}\n/&gt;</code></pre>\n<h3 id=\"theme\"><a href=\"#theme\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>theme</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">theme</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#theme\">Read about it here</a></p>\n<p>See the <a href=\"https://formidable.com/open-source/victory/guides/themes\">Themes Guide</a> for information about creating custom themes.</p>\n<p><em>default:</em> <code class=\"language-text\">theme={VictoryTheme.grayscale}</code></p>\n<p><em>Note:</em> Use the <code class=\"language-text\">dependentAxis</code> and <code class=\"language-text\">independentAxis</code> namespaces to theme axes by type. These namespaces will be merged with any props and styles supplied in the <code class=\"language-text\">axis</code> namespace.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">theme<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>VictoryTheme<span class=\"token punctuation\">.</span>material<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"tickcomponent\"><a href=\"#tickcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tickComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">tickComponent</code> prop takes a component instance which will be responsible for rendering a tick element. The new element created from the passed <code class=\"language-text\">tickComponent</code> will be provided with the following props calculated by <code class=\"language-text\">VictoryPolarAxis</code>: <code class=\"language-text\">x1</code>, <code class=\"language-text\">y1</code>, <code class=\"language-text\">x2</code>, <code class=\"language-text\">y2</code>, <code class=\"language-text\">tick</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If a <code class=\"language-text\">tickComponent</code> is not provided, <code class=\"language-text\">VictoryPolarAxis</code> will use its default <a href=\"\">Line component</a>.</p>\n<p><em>default:</em> <code class=\"language-text\">tickComponent={&lt;LineSegment type={&quot;tick&quot;}/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">tickComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>LineSegment</span> <span class=\"token attr-name\">events</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> onClick<span class=\"token punctuation\">:</span> handleClick <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"tickcount\"><a href=\"#tickcount\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tickCount</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">tickCount</code> prop specifies approximately how many ticks should be drawn on the axis. If an array of ticks is supplied in <code class=\"language-text\">tickValues</code> or <code class=\"language-text\">tickFormat</code>, the <code class=\"language-text\">tickCount</code> prop will be used to <em>downsample</em> the provided array to the specified length. If <code class=\"language-text\">tickValues</code> are not explicitly provided, this value is used by <a href=\"https://github.com/d3/d3-scale\">d3Scale</a> to calculate an <em>approximate</em> number of ticks. <a href=\"https://github.com/d3/d3-scale\">d3Scale</a> prioritizes returning “nice” values and evenly spaced ticks over an exact number of ticks. This prop must be given as a positive integer.</p>\n<h3 id=\"tickformat\"><a href=\"#tickformat\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tickFormat</h3>\n<p><code class=\"language-text\">type: array || function</code></p>\n<p>The <code class=\"language-text\">tickFormat</code> prop specifies how tick values should be labeled. The <code class=\"language-text\">tickFormat</code> prop can be given as an array of values to display for each tick, or as a function to be applied to every <code class=\"language-text\">tickValue</code>. When given as a function, <code class=\"language-text\">tickFormat</code> will be called with the following arguments: <code class=\"language-text\">tick</code> - the individual tick value, <code class=\"language-text\">index</code> - the index of the tick in the array, and <code class=\"language-text\">ticks</code> - the entire array of ticks.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  tickValues={[2.11, 3.9, 6.1, 8.05]}\n  labelPlacement=&quot;vertical&quot;\n  tickFormat={(t) =&gt; `${Math.round(t)}k`}\n/&gt;</code></pre>\n<h3 id=\"ticklabelcomponent\"><a href=\"#ticklabelcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tickLabelComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">tickLabelComponent</code> prop takes a component instance which will be used to render the axis label. The new element created from the passed <code class=\"language-text\">tickLabelComponent</code> will be supplied with the following props: <code class=\"language-text\">x</code>, <code class=\"language-text\">y</code>, <code class=\"language-text\">tick</code>, <code class=\"language-text\">verticalAnchor</code>, <code class=\"language-text\">textAnchor</code>, <code class=\"language-text\">angle</code>, <code class=\"language-text\">transform</code>, <code class=\"language-text\">style</code> and <code class=\"language-text\">events</code>. Any of these props may be overridden by passing in props to the supplied component, or modified or ignored within the custom component itself. If <code class=\"language-text\">tickLabelComponent</code> is omitted, a new <a href=\"https://formidable.com/open-source/victory/docs/victory-label\"><code class=\"language-text\">VictoryLabel</code></a> will be created with props described above.</p>\n<p><em>default:</em> <code class=\"language-text\">tickLabelComponent={&lt;VictoryLabel/&gt;}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">tickLabelComponent<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>VictoryLabel</span> <span class=\"token attr-name\">dy</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">20</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"tickvalues\"><a href=\"#tickvalues\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tickValues</h3>\n<p><code class=\"language-text\">type: array</code></p>\n<p>The <code class=\"language-text\">tickValues</code> prop explicitly specifies a set of tick values to draw on the axis. This prop should be given as an array of unique values of the same type (<em>i.e.,</em> all numbers). The <code class=\"language-text\">tickValues</code> prop is used to specify the <em>values</em> of each tick, so numeric values are typically appropriate. An array of strings or dates may be supplied for categorical and time series data respectively. Use the <a href=\"https://formidable.com/open-source/victory/docs/victory-axis#tickformat\">tickFormat</a> prop to specify how ticks should be labeled. <em>Note:</em> <code class=\"language-text\">tickValues</code> should be given as a unique array.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryPolarAxis\n  labelPlacement=&quot;vertical&quot;\n  tickValues={[2, 4, 6, 8]}\n/&gt;</code></pre>\n<h3 id=\"width\"><a href=\"#width\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>width</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p><code class=\"language-text\">VictoryPolarAxis</code> uses the standard <code class=\"language-text\">width</code> prop. <a href=\"https://formidable.com/open-source/victory/docs/common-props#width\">Read about it here</a></p>\n<p><em>default (provided by default theme):</em> <code class=\"language-text\">width={450}</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">width<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">400</span><span class=\"token punctuation\">}</span></code></pre>\n      </div>","frontmatter":{"id":19,"scope":null,"title":"VictoryPolarAxis"},"fields":{"slug":"/docs/victory-polar-axis/"}}},"pathContext":{"slug":"/docs/victory-polar-axis/"}}

/***/ })

});


